import { Component, OnInit, ViewChild } from '@angular/core';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { AlertifyService } from '../../_services/alertify.service';
import { PartParoduct } from '../../_models/PartParoduct';
import { Processor } from '../../_models/Processor';
import { SpectDetail } from '../../_models/SpectDetail';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { DataTableDirective } from 'angular-datatables';
import { RequirementData } from '../../_models/RequirementData';
import { Subject } from 'rxjs';
import { RequirementService } from '../../_services/requirement.service';
import { PriceList } from '../../_models/PriceList';
import { Supplier } from '../../_models/Supplier';

@Component({
  selector: 'app-add-price',
  templateUrl: './add-price.component.html',
  styleUrls: ['./add-price.component.css']
})
export class AddPriceComponent implements OnInit {
  model: any = {};
  roleid: any =  Number(String(localStorage.getItem('RoleID')));
  makes: PartParoduct[];
  models: PartParoduct[];
  processors: Processor[];
  hdds: SpectDetail[];
  rams: SpectDetail[];
  suppliers: Supplier[];
  prices: PriceList[];
  @ViewChild('infoModal') public infoModal: ModalDirective;
  @ViewChild(DataTableDirective) dtElement: DataTableDirective;
  dtOptions: DataTables.Settings = {
    pagingType: 'full_numbers',
    pageLength: 25,
    retrieve: true,
    order: [[0, 'asc']]
};
  dtInstance: DataTables.Api;
  isDtInitialized: boolean = false;
  dtTrigger: Subject<RequirementData> = new Subject();

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService,
    private requirementservice: RequirementService) { }

  ngOnInit(): void {
    this.loadMakes();
    this.loadProcessors();
    this.loadRAMs();
    this.loadHDDs();
    this.loadSuppliers();
    this.loadPrice();
  }
  loadMakes() {
    return this.loadspecsService.getMakes().subscribe((partproducts: PartParoduct[]) => {
      this.makes = partproducts.filter(proj => (proj.type === 1));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadModels(value: number) {
    return this.loadspecsService.getModels().subscribe((partproducts: PartParoduct[]) => {
      this.models = partproducts.filter(proj => (Number(proj.parentId) === Number(value)));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadProcessors() {
    return this.loadspecsService.getProcessors().subscribe((processors: Processor[]) => {
      this.processors = processors;
    }, error => {
      this.alertify.error(error);
    });
  }
  loadRAMs() {
    return this.loadspecsService.loadSpectDetails().subscribe((rams: SpectDetail[]) => {
      this.rams = rams.filter(proj => (proj.parentId === 1 ));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadHDDs() {
    return this.loadspecsService.loadSpectDetails().subscribe((hdds: SpectDetail[]) => {
      this.hdds = hdds.filter(proj => (proj.parentId === 2));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadSuppliers() {
    return this.loadspecsService.loadSuppliers().subscribe((suppliers: Supplier[]) => {
      this.suppliers = suppliers;
      console.log(this.suppliers);
    }, error => {
      this.alertify.error(error);
    });
  }
  loadPrice() {
    return this.requirementservice.getPrice().subscribe((prices: PriceList[]) => {
      this.prices = prices;
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });
  }
  sendRequirementPost() {
    this.model.user_id = localStorage.getItem('UserId');
    if (this.model.make_id === null) {
      this.alertify.error('Select Make');
      return;
    }
    if (this.model.model_id  === null) {
      this.alertify.error('Select Model');
      return;
    }
    if (this.model.proc_id  === null) {
      this.alertify.error('Select Processor');
      return;
    }
    if (this.model.supplier_id === null) {
      this.alertify.error('Enter Supplier Name');
      return;
    }
    if (this.model.hdd_id === null) {
      this.alertify.error('Select HDD');
      return;
    }
    if (this.model.ram_id === null) {
      this.alertify.error('Select RAM');
      return;
    }
    if (!this.model.price) {
      this.alertify.error('Enter price');
      return;
    }
    if (!this.model.qty) {
      this.alertify.error('Enter Qty');
      return;
    }
    if (!this.model.purchase_date) {
      this.alertify.error('Enter Purchase Date');
      return;
    }
    document.getElementById('postingPrice').innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
    (document.getElementById('postingPrice') as HTMLButtonElement).disabled = true;
    this.requirementservice.postPrice(this.model).subscribe(() => {
      this.loadPrice();
      this.model.make_id = null;
      this.model.model_id = null;
      this.model.proc_id = null;
      this.model.hdd_id = null;
      this.model.ram_id = null;
      this.model.supplier_name = '';
      this.model.price = '';
      document.getElementById('postingPrice').innerHTML = 'Submit';
      (document.getElementById('postingPrice') as HTMLButtonElement).disabled = false;
      this.infoModal.hide();
      this.alertify.success('Price Posted Successfull');
    }, error => {
      this.alertify.error(error);
    });
  }

}
